import { createApp } from 'vue'
import money from "v-money3";
import formulajs from "formulajs";
import App from './App.vue'

if (!window.burdardygdConfig) {
    window.burdardygdConfig = {
      jardhitaCOP: {
        radiator: 4.39,
        floor: 5.8
      },
      hitapumpaCOP: {
        radiator: 3.19,
        floor: 4.39
      }
    };
}
const app = createApp(App);
app.config.globalProperties.$burdardygdConfig = window.burdardygdConfig;
app.use(money);
app.use(formulajs);
app.mount("#burdarddygd-app");
